    font-family: 'Anton';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
    font-family: 'Open-sans';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

html,
body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
}

h1,
h2 {
    margin: 0;
    padding: 0;
    font-family: 'Anton';
    font-weight: normal;
}

p {
    margin: 8px 0;
    padding: 0;
}

button {
    outline: none;
    border-width: 2px;
    border-style: solid;
    background-color: var(--main-background-color);
    font-size: 14px;
    padding: 6px;
    border-radius: var(--main-border-radius);
    border-color: var(--accent-color);
    font-family: 'Open-sans';
}

label,
input {
    font-family: 'Open-sans';
    font-size: 15px;
}

button:hover {
    background-color: var(--accent-color);
}

.container {
    --main-background-color: rgb(223, 223, 223);
    --main-border-radius: 8px;
    --main-box-shadow: 2px 2px 8px var(--accent-color);
    --main-padding: 12px;
    --accent-color: rgb(169, 252, 252);
    --container-background-color: rgb(102, 102, 102);
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 70px 1fr;
}

#header {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    display: flex;
    align-items: center;
    padding-left: 20px;
    background-color: var(--accent-color);
}

#nav-bar {
    grid-column: 1 / 2;
    grid-row: 2 /3;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
    background-color: var(--container-background-color);
}

#nav-bar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#project-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

#hidden-project-container {
    display: none;
}

#minimize-button {

}

.project-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--main-padding);
    border: none;
    background-color: var(--main-background-color);
    box-shadow: var(--main-box-shadow);
    border-radius: var(--main-border-radius);
}

.project-title {
    margin: 0;
    padding: 0;
    font-size: 20px;
}

.project-button:hover {
    background-color: var(--accent-color);
    scale: 1.01;
}

.remove-button,
.edit-button {
    border: none;
    background-color: transparent;
}

.remove-button:hover,
.edit-button:hover {
    background-color: var(--main-background-color);
}

#list-container {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: var(--container-background-color);
    padding: 20px 15%;
}

#task-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.project-edit-form {
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
    grid-template-rows: auto 1fr 2fr;
}

.project-title-input {
    grid-column: 1 / 4;
    grid-row: 1 / 2;
    font-size: 15px;
}

.project-confirm-button {
    grid-column: 1 / 2;
    grid-row: 3 / 4;
}

.project-cancel-button {
    grid-column: 3 / 4;
    grid-row: 3 / 4;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;IACI,oBAAoB;IACpB,4CAAqC;AACzC;;AAEA;IACI,wBAAwB;IACxB,4CAAgC;AACpC;;AAEA;;IAEI,aAAa;IACb,YAAY;IACZ,SAAS;IACT,UAAU;AACd;;AAEA;;IAEI,SAAS;IACT,UAAU;IACV,oBAAoB;IACpB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,UAAU;AACd;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,mBAAmB;IACnB,8CAA8C;IAC9C,eAAe;IACf,YAAY;IACZ,wCAAwC;IACxC,iCAAiC;IACjC,wBAAwB;AAC5B;;AAEA;;IAEI,wBAAwB;IACxB,eAAe;AACnB;;AAEA;IACI,qCAAqC;AACzC;;AAEA;IACI,2CAA2C;IAC3C,yBAAyB;IACzB,kDAAkD;IAClD,oBAAoB;IACpB,kCAAkC;IAClC,gDAAgD;IAChD,YAAY;IACZ,WAAW;IACX,aAAa;IACb,8BAA8B;IAC9B,4BAA4B;AAChC;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,qCAAqC;AACzC;;AAEA;IACI,kBAAkB;IAClB,cAAc;IACd,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,aAAa;IACb,mDAAmD;AACvD;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;;AAEA;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,4BAA4B;IAC5B,YAAY;IACZ,8CAA8C;IAC9C,kCAAkC;IAClC,wCAAwC;AAC5C;;AAEA;IACI,SAAS;IACT,UAAU;IACV,eAAe;AACnB;;AAEA;IACI,qCAAqC;IACrC,WAAW;AACf;;AAEA;;IAEI,YAAY;IACZ,6BAA6B;AACjC;;AAEA;;IAEI,8CAA8C;AAClD;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,mDAAmD;IACnD,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,kCAAkC;IAClC,gCAAgC;AACpC;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB","sourcesContent":["@font-face {\n    font-family: 'Anton';\n    src: url(../assets/Anton-Regular.ttf);\n}\n\n@font-face {\n    font-family: 'Open-sans';\n    src: url(../assets/OpenSans.ttf);\n}\n\nhtml,\nbody {\n    height: 100vh;\n    width: 100vw;\n    margin: 0;\n    padding: 0;\n}\n\nh1,\nh2 {\n    margin: 0;\n    padding: 0;\n    font-family: 'Anton';\n    font-weight: normal;\n}\n\np {\n    margin: 8px 0;\n    padding: 0;\n}\n\nbutton {\n    outline: none;\n    border-width: 2px;\n    border-style: solid;\n    backgro